.body {
  display: flex;
  flex-direction: "column";
  background-color: grey;
}

.item {
  background-color: beige;
}

.block {
  width: 100px;
  height: 100px;
  background-color: grey;
}

.mounted {
  width: 200px;
  height: 200px;
  background-color: dodgerblue;
  transition: width 1s, height 1s, background-color 1s;
}
